.dialogs {
  display: grid;
  grid-template-columns: 2fr 10fr;
}

.dialogsItems {
  padding: 20px;
}

.dialogsItems .dialog {
  color: none;
}

a {
  color: white;
  text-decoration: none;
}

a.active {
  color: black;
  text-decoration: none;
}

.active {
  color: goldenrod;
}

.messages {
  padding: 10px;
}

.message {
  color: none;
}
